.top-bar {
  @include flex(row, space-between, center);

  padding-block: 24px;

  @include on-tablet {
    padding-block: 32px;
  }

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    height: 16px;

    @include hover(transform, scale(1.2));

    @include on-tablet {
      height: 24px;
    }
  }
}
